<template>
  <x-header>
    尺码对照表
  </x-header>
  <flexbox style="margin-top:30px;">
    <flexbox-item style="text-align:center;">
      <button @click="showed = 'girl'" :class="{ 'sizeButtonActive': showed == 'girl'}" class="sizeButton button button-rounded">
        女装
      </button>
    </flexbox-item>
    <flexbox-item style="text-align:center;">
      <button @click="showed = 'boy'" :class="{ 'sizeButtonActive': showed == 'boy'}" class="sizeButton button button-rounded">
        男装
      </button>
    </flexbox-item>
    <flexbox-item style="text-align:center;">
      <button @click="showed = 'child'" :class="{ 'sizeButtonActive': showed == 'child'}" class="sizeButton button button-rounded">
        童装
      </button>
    </flexbox-item>
  </flexbox>
  <div class="sub-menus" v-if="showed === 'girl'">
    <div class="sub-menus-list">
      <ul :style="girlListWidth">
        <li v-for="girl in girls">
          <button @click="selectItem(girl.value)" :class="{ 'sizeButtonActive': showedSubMenu == girl.value }" class="button button-small sizeButton button-rounded ">
            {{girl.name}}
          </button>
        </li>
      </ul>
    </div>
  </div>
  <div class="sub-menus" v-if="showed === 'boy'">
    <div class="sub-menus-list">
      <ul :style="boyListWidth">
        <li v-for="boy in boys">
          <button @click="selectItem(boy.value)" :class="{ 'sizeButtonActive': showedSubMenu == boy.value }" class="button button-small sizeButton button-rounded ">
            {{boy.name}}
          </button>
        </li>
      </ul>
    </div>
  </div>
  <div class="sub-menus" v-if="showed === 'child'">
    <div class="sub-menus-list">
      <ul :style="childListWidth">
        <li v-for="child in children">
          <button @click="selectItem(child.value)" :class="{ 'sizeButtonActive': showedSubMenu == child.value }" class="button button-small sizeButton button-rounded ">
            {{child.name}}
          </button>
        </li>
      </ul>
    </div>
  </div>
  <size-table></size-table>
</template>
<script>
  import XHeader from 'vux/components/x-header'
  import Flexbox from 'vux/components/flexbox'
  import FlexboxItem from 'vux/components/flexbox-item'
  import SizeTable from '../components/size-table'

  export default {
    data () {
      return {
        leftOptions: {
          showBack: true,
          backText: 'Back',
          preventGoBack: true
        },
        showed: 'girl',
        showedSubMenu: 'shoe',
        girls: [
          { name: '女鞋', value: 'shoe' },
          { name: '衬衫', value: 'shirt' },
          { name: '连衣裙', value: 'dress' },
          { name: '裤子', value: 'trousers' },
          { name: '内裤', value: 'underware' },
          { name: '文胸', value: 'bra' }
        ],
        boys: [
          { name: '男鞋', value: 'shoe' },
          { name: '上衣', value: 'jacket' },
          { name: '衬衫', value: 'shirt' },
          { name: '西装', value: 'suit' },
          { name: '裤子', value: 'trousers' },
          { name: '内裤', value: 'underware' }
        ],
        children: [
          { name: '童鞋', value: 'shot' },
          { name: '童装', value: 'wear' }
        ]
      }
    },
    route: {
      data (transition) {
      }
    },
    computed: {
      girlListWidth () {
        return {
          width: (this.girls.length * 8) + 'rem'
        }
      },
      boyListWidth () {
        return {
          width: (this.boys.length * 8) + 'rem'
        }
      },
      childListWidth () {
        return {
          width: (this.children.length * 8) + 'rem'
        }
      }
    },
    methods: {
      selectItem (value) {
        this.showedSubMenu = value
        alert('当前选择' + this.showed + ' -> ' + this.showedSubMenu)
      }
    },
    components: {
      XHeader,
      Flexbox,
      FlexboxItem,
      SizeTable
    }
  }
</script>



